@charset "utf-8";
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td,hr,button,article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section {margin:0;padding:0; -webkit-user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
     user-select: none;}  
body,button,input,select,textarea {font:14px \5b8b\4f53,'Helvetica Neue',Arial,'Liberation Sans',FreeSans,'Hiragino Sans GB',sans-serif\"Microsoft YaHei"\"微软雅黑";}  
h1{font-size:24px;}  
h2{font-size:22px;}  
h3{font-size:18px;}  
h1,h2,h3,h4,h5,h6 {font-weight:normal;}  
html, body, form, fieldset, p, div, h1, h2, h3, h4, h5, h6 {-webkit-text-size-adjust:none;}/*阻止旋转屏幕时自动调整字体大小*/  
textarea{resize:none;}  
/*取消按钮在inphone上的默认样式*/  
input[type=button]{-webkit-appearance:none;outline:none}   
input::-webkit-input-speech-button {display:none}  
table {border-collapse:collapse;border-spacing:0;}  
th {text-align:inherit;}  
fieldset,img {border:none;}  
abbr,acronym {border:none;font-variant:normal;}  
del {text-decoration:line-through;}  
ol,ul {list-style:none;}  
caption,th {text-align:left;}  
sub,sup {font-size:75%;line-height:0;position:relative;vertical-align:baseline;}  
sup {top:-0.5em;}  
sub {bottom:-0.25em;}  
a{color: #fff;}
ins,a,a:hover {text-decoration:none;}
.clearfix:before,.clearfix:after {content:"";display:table;}  
.clearfix:after {clear:both;overflow:hidden;}  
.clearfix {zoom:1;}  
.clear {clear:both;display:block;font-size:0;height:0;line-height:0;overflow:hidden;}  
.hide {display:none;}  
.block {display:block;}  
.outL{white-space:normal;word-break:break-all;width:100px;}  
.outH{overflow:hidden;text-overflow:ellipsis;white-space:nowrap; width:100px;}  
/*布局*/  
.fl{float:left;display:inline;}  
.fr{float:right;display:inline;}  
.cb{clear:both;}  
.cl{clear:left;}  
.cr{clear:rigth;}  
.rel{position:relative;}  
.abs{position:absolute;}  
.tac{text-align:center;}  
.tal{text-align:left;}  
.tar{text-align:right;}  
.dib{display:inline-block;}  
.vab{vertical-align:bottom;}  
.vam{vertical-align:middle;}  
.vat{vertical-align:top;}  
/*网格*/  
.box{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;height:100%;text-align:center;padding:5px 0;}  
.grid,.wrap,.grid:after,.wrap:after,.grid:before,.wrap:before{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}  
.grid{*zoom:1}  
.grid:before,.grid:after{display:table;content:"";line-height:0}  
.grid:after{clear:both}  
.grid{list-style-type:none;padding:0;margin:0}  
.grid>.grid{clear:none;float:left;margin:0 !important}  
.wrap{float:left;width:100%}  
/*网格*/  
/*flex*/  
.col{display:-webkit-box;-webkit-box-orient:vertical;display:flex;display:-webkit-flex;flex-direction:column;justify-content: space-between;}  
.row{display:-webkit-flex;display:flex;display: flex;flex-direction: row;justify-content: space-between;}  
.flex1{-webkit-box-flex:1;-webkit-flex:1;flex:1;}  
.flex2{-webkit-box-flex:2;-webkit-flex:2;flex:2;}  
.flex3{-webkit-box-flex:3;-webkit-flex:3;flex:3;}  
/*flex*/  
/*容器*/  
.wrapper{position:absolute;top:0;right:0;bottom:0;left:0;padding:5px 5px 60px 5px;overflow:auto;margin-top:44px;-webkit-overflow-scrolling:touch;}  
/*头尾*/  
header,footer{position:fixed;right:0;left:0;z-index:999;text-align:center;background:#CCCCCC;}  
header{top:0;height:1.173333rem;}  
footer{bottom:0;}  
  
/*宽度*/  
.w100p{width:100%}  
.w20p{width:20%;}  
  
/*边距*/  
.m5{margin:5px;}  
.p5{padding:5px;}  
.pt5{padding-top:5px;}
.container{
	display: flex;
    flex-direction: column;
	/*margin-top: 1.173333rem;*/
}
.content{
	flex:1;
}
p{-webkit-margin-before:0em;-webkit-margin-after:0em;}
input{outline: none;-webkit-appearance: none;border-radius: 0;border: 0;}
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}


